import {date, formatDate, editModalTitle, editModalYes} from './const';
import insertOutToItemCard from './insertOutToItemCard';

export default function getLS (formatDate, date) {
  for(let ksaN in localStorage) {
      //Поиск по ключу
      let keysKsa = (localStorage.getItem(ksaN));
      //let obj = JSON.parse(keysKsa);
      if (keysKsa !== null && keysKsa.length > 5 ) {
          //console.log(JSON.parse(keysKsa));
          // Key Value Object
          let keyObj = Object.values(JSON.parse(keysKsa));
          // Вендор
          let valVendor = keyObj[0];
          // Имя
          let valName = keyObj[1];
          // Адрес
          let valAdrress = keyObj[2];
          // След. ТО мес
          let valNextTO = keyObj[3];
          // След. ТО мес текст // 10.21
          let valNextTODateText = formatDate.format(new Date(keyObj[5]));
          // След. ТО мес // Fri Oct 15 2021 
          let nextToDate_5 = new Date(keyObj[5]);
          // Счетчик
          let mCount = (nextToDate_5.getFullYear()*12+nextToDate_5.getMonth())-(date.getFullYear()*12+date.getMonth());
          let textColor = '';
          if (mCount > 0) {
              textColor = 'text-grey';
          } else if (mCount == 0) {
              textColor = 'text-green';
          } else {
              textColor = 'text-red';
          }
          
          // Прошлое ТО 
          let lastToDate = keyObj[6];
  
  
          let outDivext = insertOutToItemCard(textColor, valNextTODateText, mCount, valAdrress, valVendor, valName, ksaN, lastToDate);
          // from ./insertOutToItemCard.js
          document.querySelector('#out').insertAdjacentHTML('afterbegin', outDivext);
          };
      };
  };
