import {date, formatDate} from './const';
import getLS from './getLocalStorage';

export default function addNew() {
    let addNewButton = document.querySelector('.add-new-but');
    let addNewWrapper = document.querySelector('.add-new-wrapper');
    let itemCardNew = document.querySelector('.item-card-new');
    
    let newFon = document.querySelector('.new-fon');
    

    addNewButton.onclick = function() {
        this.classList.add('uk-transition-slide-left');
        console.log(addNewWrapper);
        addNewWrapper.style.height = "auto";
        addNewWrapper.classList.remove("hidden");
        newFon.classList.add('fon-up');
        itemCardNew.classList.add('item-card-up');
        itemCardNew.classList.remove("uk-transition-slide-right");
        //add-new-wrapper
        //itemCardNew.classList.remove("uk-transition-slide-right");
        // document.querySelector('#out').innerHTML = '';
        // getLS (formatDate, date);
    }
}