// console.log(outArr);
export default function insertOutToItemCard(textColor, valNextTODateText, mCount, valAdrress, valVendor, valName, ksaN, lastToDate) {
        
        let out = `<div class="uk-card uk-card-default item-card">
        <span class="edit" uk-icon="icon: more-vertical; ratio: 1.2"></span>
        <div class="cancel-edit  uk-transition-slide-right"></div>
        <div class="to-edit  uk-transition-slide-right"><span>ТО-6</span></div>
        <div class="stop-edit  uk-transition-slide-right"></div>
        <div class="del-edit  uk-transition-slide-right" uk-toggle="target: #edit-modal"></div>
        <div class="to-time-left ${textColor}">${valNextTODateText} (${mCount})</div>
        <div class="item-adress">${valAdrress}</div>
        <div class="vendor hidden">${valVendor}</div>
        <div class="item-name">${valName}</div>
        <div class="item-ksa">${ksaN}</div>  
        <input type="date" class="mdc-text-field to-date hidden" value="${lastToDate}" min="2020-01-01" >
    </div>`
    
    return out;
}
